import {useEffect, useState} from 'react';
import axios from 'axios'
import './App.css';
import Coin from './components/coin'
import News from './components/news'

function App() {
  const [coins, setCoins] = useState([])
  const [allnews, setnews] = useState([])
  const [search, setSearch] = useState('')
  
  useEffect(() => {
    const url = "https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=250&page=1&sparkline=true"
    axios.get(url)
    .then(res => {
        setInterval(setCoins(res.data), 10000)
    }).catch(error => console.log(error))
    var options = {
      method: 'GET',
      url: 'https://bing-news-search1.p.rapidapi.com/news/search',
      params: {
        q: 'crypto, bitcoin, cryptocurrency',
        safeSearch: 'Off',
        textFormat: 'Raw',
        freshness: 'Day'
      },
      headers: {
        'x-bingapis-sdk': 'true',
        'x-rapidapi-host': 'bing-news-search1.p.rapidapi.com',
        'x-rapidapi-key': 'bf2bf872edmshf127b5f60e4a5dap10d874jsn6dc0ca9b0cf9'
      }
    };
    
    axios.request(options).then(function (response) {
      setnews(response.data.value);
      console.log(response.data.value)
    }).catch(function (error) {
      console.error(error);
    });
  }, [])
  
  const handleSearch = e =>{
    setSearch(e.target.value)
  }
  const filteredCoins = coins.filter(coin => 
    coin.name.toLowerCase().includes(search.toLowerCase())
    )
  const filteredNews = allnews.filter(news => 
    news.name.toLowerCase().includes(search.toLowerCase())
    )
  return (
    <div className="App">
      <header className="header"><h1>CRYPTOLOOK</h1>
      <a className="git-btn" rel="noopener noreferrer" href='https://github.com/unlikelycreator/crypto-price-tracker' target="_blank">Github</a>
      </header>
      <h1 className="heading">Todays Headlines</h1>
      <div className="news-container">
          {filteredNews.map(news =>{
            const image = news.image.thumbnail.contentUrl
            if (image) {
              return(
                <News key={news._type}
                  image = {news.image.thumbnail.contentUrl}
                  title={news.name}
                  author={news.provider[0].name}
                  description={news.description}
                  url ={news.url}
                  publishdate = {news.datePublished}
                />
                )
            }else{
              return(
                <News key={news._type}
                  title={news.name}
                  author={news.provider[0].name}
                  description={news.description}
                  url ={news.url}
                  publishdate = {news.datePublished}
                />
                )
            }
          })}
      </div>
      <div className="coin-tracker">
        <h1 className="heading">CryptoLook Price Tracker</h1>
        <div className="coin-search">
            <form className="form">
              <input onChange={handleSearch} type="text" placeholder="Search Coin Name" className="coin-input" />
            </form>
          </div>

        {filteredCoins.map(coin =>{
          return(
            <Coin className="main" key={coin.id}
            name={coin.name}
            image={coin.image}
            symbol={coin.symbol}
            volume={coin.total_volume}
            price={coin.current_price}
            priceChange={coin.price_change_percentage_24h}
            marketcap = {coin.market_cap}
            />
          )
        })}
        </div>
    </div>
  );
}

export default App;
