import React from 'react'
import './news.css'

function coin({author, description,source, publishdate, url, title, image}) {
    return (
        <div className="container">
            <div className="news-row">
                <img classname="news-img" src={image} alt="newsimg" height="150px" width="100%" />
                <div className="news-main">
                    <h1 className="news-content">{title}</h1>
                    <p className="news-description">{description}</p>
                    <a href={url} target="_blank" rel="noopener noreferrer">Read more..</a>
                    <p className="news-author"><i>Author: </i>{author}</p>
                    <p className="source">{source}</p>
                    <p className="publishdate"><i>Publish-Date:</i> {publishdate}</p>
                </div>
            </div>
        </div>
    )
}

export default coin
